#menu__toggle {
  opacity: 0; }
  @media (min-width: 900px) {
    #menu__toggle {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      border: 0;
      padding: 0;
      white-space: nowrap;
      clip-path: inset(100%);
      clip: rect(0 0 0 0);
      overflow: hidden; } }

/* стилизуем кнопку */
@media (min-width: 300px) and (max-width: 900px) {
  .menu__btn {
    display: flex;
    /* используем flex для центрирования содержимого */
    align-items: center;
    /* центрируем содержимое кнопки */
    position: fixed;
    top: 20px;
    left: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1; } }

@media (min-width: 900px) {
  .menu__btn {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; } }

/* добавляем "гамбургер" */
@media (min-width: 300px) and (max-width: 900px) {
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161; } }

@media (min-width: 300px) and (max-width: 900px) {
  .menu__btn > span::before {
    content: '';
    top: -8px; } }

@media (min-width: 300px) and (max-width: 900px) {
  .menu__btn > span::after {
    content: '';
    top: 8px; } }

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg); }

#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0); }

#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg); }

#menu__toggle:checked ~ .header__list {
  visibility: visible;
  left: 0; }

