

.header {
	@media (min-width: 300px) {
		background: url("../img/fnp-final.jpg");
		background-size: 100% 90vh;
	}
	@media (min-width: 900px) {
		height: 100vh;
		background: url("../img/fnp-9.jpg");
		background-size: 100% 100vh;
		background-position: 0 0;
		background-repeat: no-repeat;
	}


}

.header__nav {
	display: flex;
	flex-direction: column;
	height: 600px;

}

.header__list {
	@media (min-width: 300px) and (max-width: 900px) {
	  display: block;
	  position: fixed;
	  visibility: hidden;
	  top: 0;
	  left: -100%;
	  width: 300px;
	  height: 100vh;
	  margin: 0;
	  padding: 80px 0;
	  list-style: none;
	  text-align: center;
	  background-color: #ECEFF1;
	  box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
	}

	@media (min-width: 900px) {
		display: flex;
		justify-content: space-around;
		width: 100%;
		height: 45px;

		align-self: flex-end;
		list-style: none;
		padding-left: 0;

		background: linear-gradient(90deg, rgba(247,2,16,1) 0%, rgba(169,10,190,1) 72%, #a00cd6 100%);
		margin-top: 0px;
	}
}

.header__item {
	@media (min-width: 300px) and (max-width: 900px) {
		  display: block;
		  padding: 12px 24px;
		  color: black;
		  font-family: 'Arial', sans-serif;
		  font-size: 20px;
		  font-weight: 600;
		  text-align: left;
		  text-decoration: none;

		  &:hover {
		  	background-color: #CFD8DC;
		  }
	}

	@media (min-width: 900px) {
		font-family: Arial;
		font-size: 16px;
		text-transform: uppercase;
		align-self: center;
	}
}



#menu__toggle {
  opacity: 0;

  @media (min-width: 900px) {
  	@include visually-hidden;
  }
}
/* стилизуем кнопку */
.menu__btn {
  @media (min-width: 300px) and (max-width: 900px) {
  	display: flex; /* используем flex для центрирования содержимого */
  align-items: center;  /* центрируем содержимое кнопки */
  position: fixed;
  top: 20px;
  left: 20px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 1;
  }

  @media (min-width: 900px) {
  	@include visually-hidden;
  }
}
/* добавляем "гамбургер" */
.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  @media (min-width: 300px) and (max-width: 900px) {
  	display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #616161;
  }
}
.menu__btn > span::before {
  @media (min-width: 300px) and (max-width: 900px) {
  	content: '';
  	top: -8px;
  }
}
.menu__btn > span::after {
	@media (min-width: 300px) and (max-width: 900px) {
		content: '';
		top: 8px;
	}
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#menu__toggle:checked ~ .header__list {
  visibility: visible;
  left: 0;
}

