


.info {

	max-width: 100vw;
	min-height: 400px;
	margin-bottom: 100px;
	border-top: 2px solid #f7040e;
	border-bottom: 2px solid #f7040e;

	background-image: url("../img/logo.png");
	background-size: 850px 900px;
	background-position: 900px -150px;
	background-repeat: no-repeat;

}

.info__list {
	padding-left: 0;
	width: 90%;
	margin: auto;
	list-style: none;
}

.info__item {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.info__b {

	font-family: "arial";
	opacity: 1;
	text-align: left;

	@media (min-width: 900px) {
		padding-left: 50px;
		font-size: 24px;
		margin-right: 250px;

	}
}

.info__text {
	font-family: "Arial";
	opacity: 1;

	@media (min-width: 300px) and (max-width: 900px) {
		text-align: right;
	}

	@media (min-width: 900px) {
		width: 550px;
		font-size: 20px;

	}
}

